<template>
<div class="footer-container">
<div class="footer-main" :class="mainProperty">

<div class="footer-1" @click="$emit('get-control-1')">当前机型遥控器</div>
<div class="footer-2" @click="$emit('get-control-2')">其他机型遥控器</div>
<div class="foot" @click="$emit('cancel-footer')">取消</div>
</div>
<div class="footer-modal" @touchstart="$emit('cancel-footer')"/>
</div>
</template>

<script>
export default {
  mounted() {
    this.$el.querySelector(".footer-modal").addEventListener(
      "touchmove",
      function(e) {
        e.stopPropagation();
        e.preventDefault();
      },
      false
    );
  },
  props: {
    mainClass: String
  },
  watch: {
    mainClass: function(newv) {
      console.log(newv);
      if (newv === "up") {
        $(".footer-main").animate({ bottom: "0px" }, 300);
      } else {
        $(".footer-main").animate({ bottom: "-350px" }, 300);
      }
    }
  },
  computed: {
    mainProperty() {
      return this.mainClass === "up"
        ? {
            up: true,
            down: false
          }
        : {
            up: false,
            down: true
          };
    }
  },
  methods: {
    cancel() {}
  }
};
</script>


<style lang="scss" scoped>
.footer-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  .footer-modal {
    background: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    overflow: hidden;
    position: fixed;
    z-index: 2000;
  }
  .footer-main {
    width: 90%;
    // height: 11.06rem;
    margin-left: 5%;
    margin-bottom: 50px;
    text-align: center;
    position: fixed;
    bottom: -350px;
    z-index: 2001;
    overflow: hidden;
    // box-sizing: border-box;
    // box-shadow: 0 0 7px 0 rgba(64, 70, 87, 0.5);
    color: #404657;
    // background-color: rgb(244, 244, 244);
    .footer-1{
        display: flex;
        align-items: center;
        justify-content: center;
        height:3.43rem;
        width:100%;
        font-size: 16px;
        color:#00aeff;
        background-color: rgb(244, 244, 244);
        border-top-left-radius: 10px; border-top-right-radius: 10px;
        border-bottom: 1px solid #404657;
    }

    .footer-2{
    display: flex;
    align-items: center;
    justify-content: center;
    height:3.43rem;
    width:100%;
    font-size: 16px;
    color:#00aeff;
    background-color: rgb(244, 244, 244);
    border-bottom-left-radius: 10px; border-bottom-right-radius: 10px;
    margin-bottom: .74rem;
    }
    .model-body {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 70%;
      box-sizing: border-box;
      .model {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        width: 25%;
        height: 21%;
        .model-img {
          width: 46%;
        }
      }
    }
    .foot {
      display: flex;
      align-items: center;
      justify-content: center;
      height:3.43rem;
      width:100%;
      color: #404657;
      font-size: 16px;
    //   margin-top: 6%;
      border-radius: 10px;
      background-color: rgb(244, 244, 244);
      div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32%;
        height: 25%;
        // @include font-size(23px);
        background-color: #fff;
        border-radius: 50px;
        box-shadow: 0px 0px 10px 0px #dbdbdb;
      }
    }
  }
}


</style>


